.TopButton {
  color: white;
  font-size: 3rem;
  position: fixed;
  right: -35px;
  bottom: 0px;
  -webkit-transition: right 0.4s;
  -o-transition: right 0.4s;
  transition: right 0.4s;
}

.TopButton.shown {
  text-align: right;
  position: fixed;
  right: 10px;
  bottom: 0px;
  -webkit-transition: right 0.4s;
  -o-transition: right 0.4s;
  transition: right 0.4s;
}



