  color: rgba(255,255,255,1);
  font-family: 'Francois One', sans-serif;
  font-size: 3rem;
}

.daily-weather div {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 5rem;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

